<template>
  <div>
    <ul class="news-list">
      <li v-for="item in listItems" class="post">
        <!--        포인트영역-->
        <div class="points">
          {{ item.points || 0 }}
        </div>
        <!--        기타 정보 영역-->
        <div>
<!--          타이릍 영역-->
          <p class="news-title">
            <template v-if="item.domain">
              <a v-bind:href="item.url">
                {{ item.title }}
              </a>
            </template>
            <template v-else>
              <router-link v-bind:to="`item/${item.id}`">
              {{ item.title }}
              </router-link>
            </template>
          </p>
          <small class="link-text">
            {{ item.time_ago }} by
            <router-link
                v-if="item.user"
                class="link-text" v-bind:to="`/user/${item.user}`">{{ item.user }}</router-link>
                <a :href="item.url" v-else>
                  {{ item.domain }}
                </a>
          </small>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  computed: {
    listItems() {
      return this.$store.state.list;
    }
  }
}
</script>

<style scoped>
.news-list {
  margin:0;
  padding:0;
}
.post {
  list-style: none;
  display:flex;
  align-items: center;
  border-bottom:1px solid #eee;
  padding:1rem 1rem 1rem 0;
}
.points {
  width:60px;
  text-align: center;
  color:#42b883
}
.news-title {
  margin:0;
}
.link-text {
  color: #828282;
}
</style>

